/******************************
		Main Styles 
******************************/
.body {
    position: absolute;
	display: flex;
    justify-content: center; 
    align-items: center; 
    height: 100vh;
    width: 100vw;
}


.top-left-border {
	position: absolute;
	width: 5%;
	height: auto;
	top: 1.0rem;
	left: 1.0rem;
}


.bottom-right-border {
		position: absolute;
		width: 10%;
		height: auto;
		bottom: 1.2rem;
		right: 1.2rem;
		
}

form {
    display: flex;
    flex-direction: column;
    align-items: center;
    height: 50%;
	width: 100%;
    max-width: 800px;
}


/******************************
		Form Elements 
******************************/
textarea {
	color: white;
	background: #31363F;
	border-radius: 2px;
	border-color: #222831;
	height: 200px;
	outline: none;
}


input {
	color: white;
	background: #31363F;
	border-radius: 2px;
	border-color: #222831;
	height: 35px;
	outline: none;
}
	
	.row {
		display: flex;
		width: 100%;
		justify-content: space-between;
		margin-bottom: 13.5px;
		margin-right: 2px;
	}

	.col1 {
		display: flex;
		flex-direction: column;
		width: 50%;
		padding-right: 15px;
		height: 100%;
	}

	.col1_1 {
		display: flex;
		flex-direction: column;
		width: 50%;
		height: 100%;
	}

	.col2 {
		display: flex;
		flex-direction: column;
		width: 100%;
	}

	.col3 {
		display: flex;
		flex-direction: column;
		width: 100%;
		margin-top: 13.5px;
	}


/******************************
	   Button & Reactions 
******************************/
.errorMessage {
    color: red;
    font-size: 12px;
}

.submit-btn {
    background-color: #2D3250;
    color: white;
    padding: 10px 20px;
    border-radius: 5px;
	border-color: #2D3250;
    cursor: pointer;
    margin-top: 20px;
}

.alert {
    margin-top: 20px;
    width: 100%;
	color: white;
	text-align: center;
	margin-bottom: 5px;
}

.return-arrow {
	position: absolute;
	width: 4%;
	height: auto;
	top: 1.6rem;
	left: 1.7rem;
}


